import axios from 'axios'

axios.interceptors.response.use((response) => {
  let data = response.data
  return data
})
// old address
// const prod = 'https://t23-aged-care.herokuapp.com'
// const prod = 'https://quaranteam-agedcare.herokuapp.com'
const prod = 'https://care4elders.herokuapp.com'
const dev = 'http://127.0.0.1:8888'
const baseurl = process.env.NODE_ENV === 'development' ? dev : prod


//get austrilia map geoJson
// @params: open data
export const ausmap = () => {
  return axios.get('https://raw.githubusercontent.com/rowanhogan/australian-states/master/states.geojson').then(v => {
    let { features } = v
    features.forEach(item => {
      item.properties.name = item.properties.STATE_NAME
    })
    return v
  })
}

// get latest covid-19 cases
// pending
export const getcase = () => {
  return axios.get('https://services1.arcgis.com/vHnIGBHHqDR6y0CR/arcgis/rest/services/COVID19_Time_Series/FeatureServer/0/query?where=1%3D1&outFields=NSW,VIC,QLD,SA,WA,TAS,NT,ACT,ObjectId,NSW_Deaths,VIC_Deaths,QLD_Deaths,SA_Deaths,WA_Deaths,TAS_Deaths,NT_Deaths,ACT_Deaths,Total_Deaths&outSR=4326&f=json').then(v => {
    let { features } = v
    let data = features.pop().attributes
    return data
  })
}

// get loneliness persentage data
// @params: open data
export const getLone = () => {
  return axios.get('https://json.extendsclass.com/bin/8bbec6e10242')
}

// get elderly population by state
// @params: open data
export const getpop = () => {
  return axios.get('https://api.npoint.io/05e66ccc5b304b59ba5f')
}

// send request to the back-end, GET requst, 
//@params: food (string)
export const getmeal = (food) => {
  return axios.get(`${baseurl}/api/meal?food=${food}`)
}

// send request to get the Recommended Daily Nutrient Allowances
//@params: age (number)
//@params: gender (string)
export const getTarget = (age, gender) => {
  return axios.get(`${baseurl}/api/daily?age=${age}&gender=${gender}`)
}

// get food category list
// @params: none
export const getCategory = () => {
  return axios.get(`${baseurl}/api/category`).then(v => {
    return v.data
  })
}

// get recommendation both of Vegeterian and non-Vegeterian
// @params: string
export const getRecm = (str, nur) => {
  return axios.get(`${baseurl}/api/recm?veg=${str}&nutrient=${nur}`).then(v => {
    return v.data
  })
}